header{
    height: 120vh;
    padding-top: 3rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*====================CTA===================*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
}

/*========================Header Socials=========================*/

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 10rem;
}

.header_socials::after{
    content:"";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/*=========================================me==========================*/


.me{
    float: left;
margin-left: 260px;
margin-top: 100px;
width: 400px;
height: 400px;
border-radius: 100%;
overflow: hidden;
background-color: var(--color-primary);
}



/*=========scroll down======*/
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}






/*==============media queries(medium devices)======================*/

@media screen and (max-width:1024px) {

    header{
        height: 68vh;

    }
    
}

/*==============media queries(small devices)======================*/

@media screen and (max-width:600px) {

    header{
        height: 100vh;

    }

    .header_socials,.scroll_down{
        display: none;
    }
    
}



